import Image from 'react-bootstrap/Image';
import { useState } from 'react';
import Modal from './Modal';
import './Gallery.css';

function Gallery() {

    const [modalOpen, setModalOpen] = useState(false);

    const [imageList, setImageList] = useState([
    ]);

    const openModal = () => {
      setModalOpen(true);
    };
  
    const closeModal = () => {
      setModalOpen(false);
    };

    return(
        
        <div className='itemBox' id='galleryDiv'>
            <div className='galleryRow'>
                {modalOpen && (
                    <Modal images={imageList} closeModal={closeModal} />
                )}
    
                <div className='galleryColumn'>
                    <Image src="./photo/thumb/1-1.jpg" fluid thumbnail onClick={
                        () => {setImageList(["./photo/1-1.jpg","./photo/1-2.jpg"]);openModal();}}/>
                </div>
                <div className='galleryColumn'>
                    <Image src="./photo/thumb/2-1.jpg" fluid thumbnail onClick={
                        () => {setImageList(["./photo/2-1.jpg", "./photo/2-2.jpg"]);openModal();}}/>
                </div>
                
            </div>

            <div className='galleryRow'>
                <div className='galleryColumn'>
                    <Image src="./photo/thumb/3-1.jpg" fluid thumbnail onClick={
                        () => {setImageList(["./photo/3-1.jpg", "./photo/3-2.jpg"]);openModal();}}/>
                </div>
                <div className='galleryColumn'>
                    <Image src="./photo/thumb/4-1.jpg" fluid thumbnail onClick={
                        () => {setImageList(["./photo/4-1.jpg", "./photo/4-2.jpg"]);openModal();}}/>
                </div>
            </div>  

            <div className='galleryRow'>
                <div className='galleryColumn'>
                    <Image src="./photo/thumb/5-1.jpg" fluid thumbnail onClick={
                        () => {setImageList(["./photo/5-1.jpg", "./photo/5-2.jpg"]);openModal();}}/>
                </div>
                <div className='galleryColumn'>
                    <Image src="./photo/thumb/6-1.jpg" fluid thumbnail onClick={
                        () => {setImageList(["./photo/6-1.jpg", "./photo/6-2.jpg"]);openModal();}}/>
                </div>
            </div>  


        </div>
    );
}

export default Gallery;