import React, { useState, useEffect } from 'react';
import {CopyToClipboard} from "react-copy-to-clipboard/src";

import './ContactModal.css'; // Make sure to create and link the CSS file for styling.

const Modal = ({ closeModal, name, accountNumber }) => {

  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (e.target.classList.contains('Accountmodal-background')) {
        closeModal();
      }
    };

    window.addEventListener('click', handleOutsideClick);

    return () => {
      window.removeEventListener('click', handleOutsideClick);
    };
  }, [closeModal, name, accountNumber]);

  return (
    <div className="Accountmodal-background">
      <div className="Accountmodal-content" onClick={(e) => e.stopPropagation()}>
            {name}<br/>{accountNumber}
            <CopyToClipboard text={accountNumber} onCopy={() => alert("계좌번호가 복사되었습니다.")}>
                <button type="button" class="btn btn-primary">계좌번호 복사하기</button>
            </CopyToClipboard>
            
      </div>
    </div>
  );
};

export default Modal;
