// Modal.js

import React, { useState, useEffect } from 'react';
import './Modal.css'; // Make sure to create and link the CSS file for styling.

const Modal = ({ images, closeModal }) => {
  const [currentImage, setCurrentImage] = useState(0);

  const slideLeft = () => {
    setCurrentImage((prevImage) => (prevImage - 1 + images.length) % images.length);
  };

  const slideRight = () => {
    setCurrentImage((prevImage) => (prevImage + 1) % images.length);
  };

  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (e.target.classList.contains('modal-background')) {
        closeModal();
      }
    };

    const handleSlideKeyPress = (e) => {
      if (e.key === 'ArrowLeft') {
        slideLeft();
      } else if (e.key === 'ArrowRight') {
        slideRight();
      }
    };

    window.addEventListener('click', handleOutsideClick);
    window.addEventListener('keydown', handleSlideKeyPress);

    return () => {
      window.removeEventListener('click', handleOutsideClick);
      window.removeEventListener('keydown', handleSlideKeyPress);
    };
  }, [closeModal, currentImage]);

  return (
    <div className="modal-background">
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="image-container">
          <button className="arrow-button left-arrow" onClick={slideLeft}>
            &lt;
          </button>
          <img
            src={images[currentImage]}
            alt={`Image ${currentImage + 1}`}
            className="modal-image"
          />
          <button className="arrow-button right-arrow" onClick={slideRight}>
          &gt;
          </button>
        </div>
        <div className="image-navigation">
          <span>{`${currentImage + 1}/${images.length}`}</span>
        </div>
      </div>
    </div>
  );
};

export default Modal;
