import React from 'react';

import './App.css';

import Gallery from './Gallery';
import Contacts from './Contacts';

import Image from 'react-bootstrap/Image';
import Button from 'react-bootstrap/Button';

import 'bootstrap/dist/css/bootstrap.min.css';

/* The following line can be included in your src/index.js or App.js file */
import './App.scss';

function App() {

  var isMobile = /Mobi/i.test(window.navigator.userAgent);

  if (!isMobile) {
    window.alert('모바일 청접장에 방문해주셔서 감사합니다.\n본 페이지는 모바일 전용 페이지로,\n스마트폰 등에서 접속하여 주시면 감사하겠습니다.');

  } else {
    return (
      <div id='mainDiv'>
        <div className='headerBox baseColor'/>

        <div className='itemBox' id='pageOne'>
          <Image src="page1.png" fluid/>
        </div>

        <div className='itemBox'>
          <Image src="page2.png" fluid />
        </div>

        <Gallery/>

        <div className='itemBox'>
          <Image src="page4.png" fluid />
        </div>

        <Contacts/>

        <div className='lastBox baseColor'/>

        <Button
          id='topButton'
          onClick={
            () => {document.getElementById('mainDiv').scrollTo({top: 0, behavior: 'instant'}); }
          }
        />

      </div>
    );
  }
}

export default App;
